import {
  AddArticleCommnetResponse,
  ArticleCommentResponse,
  ArticleDetailResponse,
} from './../types/data.d'
import request from '@/utils/request'

// 
export function getArticleDetail(id: string): Promise<ArticleDetailResponse> {
  return request.get(`/articles/${id}`)
}

// 
export function follow(id: string) {
  return request.post('/user/followings', {
    target: id,
  })
}
// 
export function unFollow(id: string) {
  return request.delete(`/user/followings/${id}`)
}

// 
export function fav(art_id: string) {
  return request.post('/article/collections', {
    target: art_id,
  })
}
// 
export function unFav(art_id: string) {
  return request.delete(`/article/collections/${art_id}`)
}

// 
export function like(art_id: string) {
  return request.post('/article/likings', {
    target: art_id,
  })
}
// 
export function unLike(art_id: string) {
  return request.delete(`/article/likings/${art_id}`)
}

// 
/**
 *
 * @param type ac a-(article)c-(comment)
 * @param id idid
 * @param offset id
 * @returns
 */
export function getComments(
  type: string,
  id: string,
  offset: string | null
): Promise<ArticleCommentResponse> {
  return request.get('/comments', { params: { type, source: id, offset } })
}

// 
type DataComment = {
  target: string // ididid
  content: string // /
  art_id?: string // idid
}
export function addComment(
  data: DataComment
): Promise<AddArticleCommnetResponse> {
  return request.post('/comments', data)
}

// 
export function likeComment(com_id: string) {
  return request.post('/comment/likings', {
    target: com_id,
  })
}
// 
export function unLikeComment(com_id: string) {
  return request.delete(`/comment/likings/${com_id}`)
}
