/**
 * 
 */
import cla from 'classnames'
type Props = {
  type: string
  className?: string
  onClick?: (e: React.MouseEvent<SVGSVGElement, MouseEvent>) => void
}
function Icon({ type, className, onClick }: Props) {
  return (
    <svg
      onClick={onClick}
      className={cla('icon', className)}
      aria-hidden="true"
    >
      {/*  iconbtn_like_sel  icon */}
      <use xlinkHref={`#${type}`}></use>
    </svg>
  )
}

export default Icon
