// 
import {
  Button,
  List,
  DatePicker,
  NavBar,
  Popup,
  Toast,
  Dialog,
} from 'antd-mobile'
import classNames from 'classnames'

import styles from './index.module.scss'
import { useDispatch, useSelector } from 'react-redux'
import { useEffect, useRef, useState } from 'react'
import { getUserEditAction, updateUserAction } from '@/store/actions/profile'
import { RootState } from '@/types/store'
import EditInput from './components/EditInput'
import EditList from './components/EditList'
import { uploadPhotoApi } from '@/api/profile'
import dayjs from 'dayjs'
import { logoutAction } from '@/store/actions/login'
import { useHistory } from 'react-router-dom'

const Item = List.Item

type InputProps = {
  type: '' | 'name' | 'intro'
  value: string
  show: boolean
}

type ListProps = {
  type: '' | 'gender' | 'photo'
  show: boolean
}

const ProfileEdit = () => {
  const dispatch = useDispatch()
  const history = useHistory()
  // 1. 
  useEffect(() => {
    dispatch(getUserEditAction() as any)
  }, [])

  // 2. 
  const { edit } = useSelector((state: RootState) => state.profile)
  const { photo, name, gender, birthday, intro } = edit

  // 3. 
  const [inputVisible, setInputVisible] = useState<InputProps>({
    type: '', //  'name' | 'intro'
    value: '', // 
    show: false, // 
  })
  // 
  const openName = () => {
    // args
    setInputVisible({
      type: 'name',
      value: name,
      show: true,
    })
  }

  // 
  const openIntro = () => {
    setInputVisible({
      type: 'intro',
      value: intro || '',
      show: true,
    })
  }

  // 
  const closeInput = () => {
    setInputVisible({
      type: '',
      value: '',
      show: false,
    })
  }

  //  => redux
  const updateUser = async (
    type: string,
    data: string | number,
    close: () => void
  ) => {
    if (type === 'photo') {
      inputRef.current?.click()
    } else {
      // type
      await dispatch(updateUserAction({ [type]: data }) as any)
      Toast.show({
        content: '',
        duration: 1000,
      })
      // if (type === 'name' || type === '' || type === 'intro') {
      //   closeInput()
      // } else {
      //   closeList()
      // }
      close()
    }
  }

  

  // 4. 
  const inputRef = useRef<HTMLInputElement>(null)
  // 
  const [listProps, setListProps] = useState<ListProps>({
    type: '',
    show: false,
  })

  // 
  const openGender = () => {
    setListProps({
      type: 'gender',
      show: true,
    })
  }

  // 
  const openPhoto = () => {
    setListProps({
      type: 'photo',
      show: true,
    })
  }

  // 
  const closeList = () => {
    setListProps({
      type: '',
      show: false,
    })
  }

  // 
  const uploadPhoto = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files?.length) return
    // 
    const fm = new FormData()
    fm.append('photo', e.target.files[0])
    const {
      data: { photo },
    } = await uploadPhotoApi(fm)
    await dispatch(updateUserAction({ photo }) as any)
    Toast.show({
      content: '',
    })
    closeList()
  }

  // 5. 
  const [showBir, setShowBir] = useState(false)
  const openBir = () => {
    setShowBir(true)
  }

  const closeBir = () => {
    setShowBir(false)
  }

  // 
  const onSelBir = (sel: Date) => {
    console.log(sel)
    // 
    const selTime = dayjs(sel).format('YYYY-MM-DD')
    updateUser('birthday', selTime, closeBir)
  }

  // 6. 
  const logout = () => {
    Dialog.confirm({
      title: '?',
      confirmText: '',
      onConfirm: () => {
        // console.log('')
        dispatch(logoutAction() as any)
        history.replace('/login')
      },
    })
  }

  return (
    <div className={styles.root}>
      <div className="content">
        {/*  */}
        <NavBar
          onBack={() => history.go(-1)}
          style={{
            '--border-bottom': '1px solid #F0F0F0',
          }}
        >
          
        </NavBar>

        <div className="wrapper">
          {/*  */}
          <List className="profile-list">
            {/*  */}
            <Item
              onClick={openPhoto}
              extra={
                <span className="avatar-wrapper">
                  <img width={24} height={24} src={photo} alt="" />
                </span>
              }
              arrow
            >
              
            </Item>
            <Item onClick={openName} arrow extra={name}>
              
            </Item>
            <Item
              arrow
              extra={
                <span
                  onClick={openIntro}
                  className={classNames('intro', intro?'normal':'')}
                >
                  {intro || ''}
                </span>
              }
            >
              
            </Item>
          </List>

          <List className="profile-list">
            <Item onClick={openGender} arrow extra={gender === 0 ? '' : ''}>
              
            </Item>
            <Item onClick={openBir} arrow extra={birthday}>
              
            </Item>
          </List>

          <DatePicker
            visible={showBir}
            value={new Date(birthday)}
            title=""
            onCancel={closeBir}
            onConfirm={onSelBir}
            min={new Date(1900, 0, 1, 0, 0, 0)}
            max={new Date()}
          />
        </div>

        <div className="logout">
          <Button onClick={logout} className="btn">
            
          </Button>
        </div>
      </div>
      {/*  */}
      <Popup visible={inputVisible.show} position="right">
        <EditInput
          type={inputVisible.type}
          value={inputVisible.value}
          onClose={closeInput}
          updateUser={updateUser}
        />
      </Popup>
      {/*  */}
      <Popup visible={listProps.show} onMaskClick={closeList} position="bottom">
        <EditList
          type={listProps.type}
          onClose={closeList}
          onUpdate={updateUser}
        />
      </Popup>
      {/*  =>  */}
      {/*  */}
      <input ref={inputRef} onChange={uploadPhoto} type="file" hidden />
    </div>
  )
}

export default ProfileEdit
