import axios from 'axios'
import store from '@/store'

import customHistory from './history'
import { Toast } from 'antd-mobile'
import { clearToken, isAuth, setToken } from './auth'

// axios
const http = axios.create({
  // baseURL: 'http://toutiao.itheima.net/v1_0',
  baseURL:'/backend'

})

// 
http.interceptors.request.use((config) => {
  // token
  
  const {
    login: { token },
  } = store.getState()
  if (token) {
    // console.log(token)
    // 
    config.headers.Authorization = `Bearer ${token}`
  }

  return config
})

// 
http.interceptors.response.use(
  (res) => {
    return res.data
  },
  //  err response config
  
  async (error) => {
    // 401token
    if (error.response.status === 401) {
      try {
        // 
        if (!isAuth) {
          throw Error(error)
        }
        // 
        const { refresh_token } = store.getState().login
        // token
        const { data } = await axios.put(
          'http://toutiao.itheima.net/v1_0/authorizations',
          null,
          {
            headers: {
              Authorization: `Bearer ${refresh_token}`,
            },
          }
        )
        // tokenredux
        const newTokens = {
          token: data.data.token,
          refresh_token,
        }
        // 
        setToken(newTokens)
        // store
        store.dispatch({ type: 'login/token', payload: newTokens })
        //  url base-url promise 
        return http(error.config)
      } catch (error) {
        // token
        // 
        store.dispatch({ type: 'login/logout' })
        // token
        clearToken()
        Toast.show({
          content: '',
          icon: 'fail',
          afterClose: () => {
            customHistory.replace({
              pathname: '/login',
              state: { from: customHistory.location.pathname },
            })
          },
        })
        return Promise.reject(error)
      }
    }
    return Promise.reject(error)
  }
)

export default http
