import { Link, useHistory } from 'react-router-dom'

import Icon from '@/components/icon'
import styles from './index.module.scss'
import { useDispatch, useSelector } from 'react-redux'
import { useEffect } from 'react'
import { getUserAction } from '@/store/actions/profile'
import { RootState } from '@/types/store'
import { useRedux } from '@/hooks'

const Profile = () => {
  const history = useHistory()
  const dispatch = useDispatch()

  // 1. redux
  // useEffect(() => {
  //   dispatch(getUserAction() as any)
  // }, [])

  // 2. redux
  // const user = useSelector<RootState, RootState['profile']>(
  //   (state) => state.profile
  // )
  const { user } = useRedux(getUserAction, 'profile')
  const { photo, name, like_count, follow_count, fans_count, art_count } = user

  return (
    <div className={styles.root}>
      <div className="profile">
        {/*  */}
        <div className="user-info">
          <div className="avatar">
            <img src={photo} alt="" />
          </div>
          <div className="user-name">{name}</div>
          {/*  */}
          {/* router6 */}
          <Link to="/profile/edit">
             <Icon type="iconbtn_right" />
          </Link>
        </div>

        {/*  */}
        <div className="read-info">
          <Icon type="iconbtn_readingtime" />
          
          <span>10</span>
          
        </div>

        {/*  -  */}
        <div className="count-list">
          <div className="count-item">
            <p>{art_count}</p>
            <p></p>
          </div>
          <div className="count-item">
            <p>{follow_count}</p>
            <p></p>
          </div>
          <div className="count-item">
            <p>{fans_count}</p>
            <p></p>
          </div>
          <div className="count-item">
            <p>{like_count}</p>
            <p></p>
          </div>
        </div>

        {/*  -  */}
        <div className="user-links">
          <div className="link-item">
            <Icon type="iconbtn_mymessages" />
            <div></div>
          </div>
          <div className="link-item">
            <Icon type="iconbtn_mycollect" />
            <div></div>
          </div>
          <div className="link-item">
            <Icon type="iconbtn_history1" />
            <div></div>
          </div>
          <div className="link-item">
            <Icon type="iconbtn_myworks" />
            <div></div>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="more-service">
        <h3></h3>
        <div className="service-list">
          <div className="service-item">
            <Icon type="iconbtn_feedback" />
            <div></div>
          </div>
          <div className="service-item" onClick={() => history.push('/chat')}>
            <Icon type="iconbtn_xiaozhitongxue" />
            <div></div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Profile
